.groups {
  font-size: 20px;
}
.project-title {
  display: flex;
  align-items: center;
}

p {
  font-size: 1rem;
  font-family: sans-serif;
  margin: 30px;
}

h2 {
  font-size: 1.5rem;
  font-family: sans-serif;
  margin: 50px;
  text-align: center;
}

h3 {
  margin: 30px;
  display: inline-block;
}

h4 {
  font-size: 1rem;
  font-family: sans-serif;
}

.topnav {
  background-color: #333;
  overflow: hidden;
}

/* Style the links inside the navigation bar */
.topnav a {
  float: left;
  color: #f2f2f2;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;
}

/* Change the color of links on hover */
.topnav a:hover {
  background-color: grey;
  color: black;
}

/* Add a color to the active/current link */
.topnav a.active {
  background-color: #6b9e1f;
  color: white;
}

.overview-table {
  padding-top: 60px;
  padding-bottom: 60px;
}

.DP-3T {
  background-color: #e8e8e8;
  padding-top: 30px;
  padding-bottom: 30px;
}

.PEPP-PT {
  padding-top: 30px;
  padding-bottom: 30px;
}

.DCTS {
  padding-top: 30px;
  padding-bottom: 30px;
}

.img-responsive {
  width: 30px;
  height: 30px;
  border: 0;
}

.diagram {
  max-width: 75%;
  height: auto;
  object-fit: contain;
  padding: 50px;
}

.graphic {
  width: auto;
  text-align: center;
  padding: 20px;
}

.paragraph {
  width: 75%;
  border: 5px solid #6b9e1f;
  padding: 50px;
  margin: 0 auto;
}

#member {
  padding: 50px;
}

table {
  border-collapse: collapse;
  width: 70%;
  text-align: center;
  padding: 50px;
  margin: 0 auto;
  table-layout: fixed;
}

table,
th,
td {
  border: 1px solid lightgray;
}

th {
  font-size: large;
  height: 50px;
  background-color: #6b9e1f;
  color: white;
}

th,
td {
  padding: 15px;
}

tr:nth-child(even) {
  background-color: #f5f5f5;
}
